import React from 'react'
import styled from 'styled-components'
import theme from '@style/theme'
import { H3 } from '@style/global'
//import { useCVItemQuery } from '../query/itemquery';

const Item = props => {
  return (
    <ItemBox>
      <H3>
        <b>{props.title}</b>
      </H3>
      {props.data.allCvJson.edges
        .filter(fortype => fortype.node.type === props.selectType)
        .map(({ node, i }) => (
          <>
            <Bloc key={i} className="bloc">
              <Title>{node.title}</Title>
              <Org>{node.org}</Org>
              <Location>
                {node.yrStart ? (
                  <>
                    {node.yrStart} - {node.yrEnd} | {node.location}
                  </>
                ) : (
                  <>
                    {node.yrEnd} | {node.location}
                  </>
                )}
              </Location>
              <Desc>
                {node.desc.map((item, i) => {
                  return (
                    <LsDesc key={i}>
                      {item}
                      <br />
                    </LsDesc>
                  )
                })}
              </Desc>
            </Bloc>
          </>
        ))}
    </ItemBox>
  )
}

export default Item

const ItemBox = styled.div``

const Bloc = styled.div`
  margin: 10px 0;
  padding: 7px 0 17px 0;
  border-bottom: 1px solid ${theme.color.grayscale.S400};
  :last-of-type {
    border-bottom: none;
  }
`

const Title = styled.div`
  ${theme.fontSize.base};
  font-family: ${theme.font.primary};
  font-weight: bold;
`
const Org = styled.div`
  ${theme.fontSize.base};
  margin-bottom: 5px;
`
const Location = styled.div`
  ${theme.fontSize.sm};
  color: ${theme.color.grayscale.S800};
`
const Desc = styled.div`
  margin: 0;
`
const LsDesc = styled.div`
  ${theme.fontSize.sm};
  color: ${theme.color.grayscale.S800};
  ::before {
    content: '⁃ ';
  }
`
